@import "var.scss";
@import "mixins.scss";

// --------- MEDIA-------//

@media screen and (min-width: 320px) and (max-width: 500px) {

    #header{
        nav{
            .responsive_services{
                display: inherit;
                margin-left: 95px;
                a{
                    text-decoration: none;
                    color: $main_text_color;
                }
                ul li a{
                    text-align: left;
                    padding-left: 0px;
                }
                li {
                    list-style-type: none;
                  }
            }
            .logo_company{
                margin-left: 60px;
            }
            .navbar-nav{
                display: none;
    ////------Dropdown menu----///////
                .dropdown-menu{
                 display: none;
                }
            }
            .changeCurency, .changeLanguage a{
              display: none;
            }
            .social_items{
                margin-left: 5px;
            }
        } 
    }


//////////////    
    #slider{
        .slider{
             .left_text{
             left: 20px;
             }
            .right_text{
             right: 20px;
             }
                .carousel-caption {
                    margin-bottom: 70px;
                 h3 {
                     font-size: 25px;
                     margin-bottom: 45px;
                 }
                }
     }    
 }
 .follow_info{
     min-width: 100%;
 margin-top: 20px;
     }
     .popular_items_card{
         margin-left: 40px;
     }


#popular_items {
        .popular_items_card, .outPut{
            .card{
             margin: 0px 0px 20px -28px;
            }
     }
    }

#baner{
    .baner{ 
        width: 100%;
        height: 200px;
        background-position: center center;
        }
    .col-12{
        margin-top: 15%;
    }
     h1{
         color: $main_baground_color;
         font-weight: 700;
         font-size: 28px;
     }
}

#featured_carousel{ 
    display: none;
}


#benefints_content{
    padding:20px;
    font-size: $main_font_size;
}

footer{
    font-size: $main_font_size;
    .footer{
        height:100%;
        width: 100%;
        .categoriest_items{
            display: none;
        }
        .dropdown{
            display: block;
            margin-left: 20px;
            a{
                color: $main_text_color;
            }
                }
      }
      .social_items{
          margin: 15px 0px 10px 60px;
      } 
}

}





